/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container {
  position: relative;
  width: 100%;
  max-width: 2000px;
  margin-right: auto;
  margin-left: auto;
  padding: 10px;
  box-sizing: border-box;
}
@media screen and (min-width: 1024px) {
  .container {
    padding: 50px;
  }
}

section,
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border: 1px solid red;
}

div,
.column {
  flex-basis: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (min-width: 1024px) {
  div,
.column {
    flex: 1;
    padding-right: 50px;
  }
  div:last-child,
.column:last-child {
    padding-right: 0;
  }
  .p-column div,
.p-column .column {
    padding-top: 45px;
    padding-bottom: 45px;
    padding-left: 50px;
    padding-right: 50px;
  }
}

