.align-start {
  align-items: flex-start;
}

.align-end {
  align-items: flex-end;
}

.align-center {
  align-items: center;
}

.align-auto {
  align-items: auto;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 a {
  text-decoration: none;
}

nav button {
  display: block;
}
@media screen and (min-width: 1024px) {
  nav button {
    display: none;
  }
}

nav ul {
  display: none;
  flex-direction: column;
  position: absolute;
  left: 0;
  background-color: white;
  z-index: 1;
  list-style: none;
  width: 100%;
}
@media screen and (min-width: 1024px) {
  nav ul {
    display: flex;
    flex-direction: row;
    position: relative;
    left: auto;
  }
}

nav ul.show {
  display: flex;
}

main {
  display: flex;
  flex-direction: column;
  flex: 1;
  position: relative;
  width: 100%;
  max-width: 2000px;
  margin-right: auto;
  margin-left: auto;
  padding: 10px;
  box-sizing: border-box;
}
@media screen and (min-width: 1024px) {
  main {
    padding: 50px;
  }
}
@media screen and (min-width: 1024px) {
  main {
    flex-direction: row;
  }
}

article {
  flex-basis: 100%;
}
@media screen and (min-width: 1024px) {
  article {
    flex: 6;
  }
}

aside {
  flex-basis: 100%;
}
@media screen and (min-width: 1024px) {
  aside {
    flex: 4;
  }
}

footer {
  text-align: center;
}